import React from "react";
import {
  Box,
  Button,
  List,
  ListItem,
  ListItemText,
  IconButton,
  SwipeableDrawer,
  useMediaQuery,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import crop from "../../assets/crop.png";
import { NavLink } from "react-router-dom";
import { LogoTypography } from "./Styled-component";

export default function SwipeableTemporaryDrawer({ data }) {
  const myDisplay = useMediaQuery("(max-width: 900px)");

  const [state, setState] = React.useState({
    left: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <Box
      sx={{
        textAlign: "center",
        background: "#2B2B2B",
        height: "100%",
        width: anchor === "top" || anchor === "bottom" ? "auto" : 250,
      }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <Box>
        <NavLink
          to="/"
          smooth="true"
          style={({ isActive }) => ({
            textDecoration: "none",
            color: isActive ? "#79B5D9" : "#000000",
          })}
        >
          {/* <LogoTypography>Uzair A.</LogoTypography> */}
          <img
            style={{ maxWidth: myDisplay ? "40%" : "100%", marginTop: "6%" }}
            src={crop}
            alt=""
          />
        </NavLink>
      </Box>
      <List sx={{ marginLeft: "30%" }}>
        {data.map((item) => {
          let string = item.replace(/\s+/g, "").trim();
          let sentence = string.toLowerCase();
          return (
            <Box key={item} sx={{ color: "white" }}>
              <NavLink
                to={`/${sentence}`}
                smooth="true"
                style={{
                  textDecoration: "none",
                  color: "#FFFFFF",
                }}
              >
                <ListItem button>
                  <ListItemText primary={item} />
                </ListItem>
              </NavLink>
            </Box>
          );
        })}
      </List>
    </Box>
  );

  return (
    <Box>
      {["left"].map((anchor) => (
        <React.Fragment key={anchor}>
          <Box onClick={toggleDrawer(anchor, true)}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              color="inherit"
            >
              <MenuIcon style={{ color: "#FFFFFF" }} />
            </IconButton>
          </Box>
          <SwipeableDrawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
    </Box>
  );
}
